import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {PlusCircle} from "react-feather";
import {NumericFormat} from "react-number-format";
import Form from "../../../containers/form";
import {get} from "lodash";
import {Input, Select, Datepicker} from "../../../containers/form/components";
import dayjs from "dayjs";
import {getDefaultValue} from "../../../utils";
import {useTranslation} from "react-i18next";



const PatientGroupsContainer = () => {
    const [open, setOpen] = useState(false)
    const {t} = useTranslation()
    const columns = [
        {
            title: 'ID',
            key: 'id',
            classnames: '!w-[80px] !min-w-0'
        },
        {
            title: t('Month'),
            key: 'month',
        },
        {
            title: t('Code'),
            key: 'code',
        },
        {
            title: t('Ratio'),
            key: 'ratio',
        },
        {
            title: t('Gender'),
            key: 'gender',
        },
        {
            title: t('From age'),
            key: 'from_age_in_month',
            render: ({value}) => <NumericFormat displayType={'text'} thousandSeparator={' '} value={value}/>
        },
        {
            title: t('To age'),
            key: 'to_age_in_month',
            render: ({value}) => <NumericFormat displayType={'text'} thousandSeparator={' '} value={value}/>
        },
    ]
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Группы населения")}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={() => setOpen(true)}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <PlusCircle size={20} className={'mr-1.5'}/>
                        {t("Добавить")}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        ModalBody={(onSubmit, defaultValues = {}) => <Form onSubmit={({data, ...rest}) => onSubmit({
                            data: {
                                ...data,
                                gender: get(data, 'gender.value'),
                                month: dayjs(get(data, 'month')).format("YYYY-MM")
                            }, ...rest
                        })}
                                                                           defaultValues={{
                                                                               ...get(defaultValues, 'data.data', {}),
                                                                               month: dayjs(get(get(defaultValues, 'data.data', {}), 'month')),
                                                                               gender: getDefaultValue([{
                                                                                   value: 'male',
                                                                                   label: t('Male')
                                                                               }, {
                                                                                   value: 'female',
                                                                                   label: t('Female')
                                                                               }], get(get(defaultValues, 'data.data', {}), 'gender'))
                                                                           }}>
                            <Input name={'code'} placeholder={t('Code')} label={t('Code')}
                                   params={{required: true}}/>
                            <Input name={'ratio'} property={{type: 'number'}} placeholder={t('Ratio')}
                                   label={t('Ratio')}
                                   params={{required: true}}/>
                            <Select name={'gender'} label={t('Gender')} params={{required: true}}
                                    options={[{value: 'male', label: t('Male')}, {value: 'female', label: t('Female')}]}/>
                            <Input name={'from_age_in_month'} property={{type: 'number', step: 1}}
                                   placeholder={t('From age')}
                                   label={t('From age in month')}
                                   params={{required: true}}/>
                            <Input name={'to_age_in_month'} property={{type: 'number', step: 1}} placeholder={t('To age')}
                                   label={t('To age in month')}
                                   params={{required: true}}/>
                            <Datepicker property={{showMonthYearPicker: true}} dateFormat={'yyyy-MM'} name={'month'}
                                        label={t('Month')}
                                        params={{required: true}}/>
                            <button type={'submit'}
                                    className={'w-full p-3.5 rounded-lg bg-primary block w-full text-white font-bold text-center h-12 mt-6'}>
                                {open ? t('Create') : t('Save')}
                            </button>
                        </Form>}
                        openCreateModal={open}
                        setOpenCreateModal={setOpen}
                        hasActionColumn
                        listKey={KEYS.patientGroups} url={URLS.patientGroups}
                        columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default PatientGroupsContainer;